"use client"

import { useState } from "react"
import Navbar from "@/components/navbar"
import Footer from "@/components/footer"
import EarthLoader from "@/components/loading-spinner"

interface WildfireResult {
  image_used: string
  checkpoint: string
  probability: number
  prediction: number
  label: string
  threshold: number
  error?: string
}

export default function Home() {
  const [result, setResult] = useState<WildfireResult | null>(null)
  const [isLoading, setIsLoading] = useState(false)

  const runWildfireDetection = async () => {
    setIsLoading(true)
    setResult(null)

    try {
      const response = await fetch("http://localhost:5000/predict")
      const data = await response.json()

      setTimeout(() => {
        setResult(data)
        window.scrollTo({ top: 0, behavior: "smooth" })
        setIsLoading(false)
      }, 2000) // optional 2s animation delay
    } catch (error) {
      setResult({ error: "Failed to connect to Flask server." } as any)
      setIsLoading(false)
    }
  }

  const handleRecalculate = () => {
    setResult(null)
    window.scrollTo({ top: 0, behavior: "smooth" })
  }

  return (
    <div className="min-h-screen flex flex-col bg-gradient-to-br from-background via-background to-primary/5">
      <Navbar />

      {isLoading && <EarthLoader />}

      {/* Hero Section */}
      <section className="flex-1 pt-20 pb-12 px-4 md:px-8">
        <div className="max-w-4xl mx-auto text-center">
          <h1 className="text-5xl md:text-6xl font-bold mb-4">
            Wildfire Detection <span className="text-primary">AI</span>
          </h1>

          <p className="text-lg md:text-xl text-muted-foreground mb-8 max-w-2xl mx-auto">
            Analyze the most recent satellite image and detect wildfire risk instantly.
          </p>

          <button
            onClick={runWildfireDetection}
            className="bg-primary hover:bg-primary/90 text-primary-foreground font-semibold px-8 py-3 rounded-lg shadow-smooth hover:shadow-smooth-lg transition-all duration-300"
          >
            Run Wildfire Analysis
          </button>
        </div>
      </section>

      {/* Result Section */}
      {result && !result.error && (
        <section className="py-12 px-4 md:px-8">
          <div className="max-w-3xl mx-auto bg-card p-8 rounded-xl shadow-smooth border">
            <h2 className="text-3xl font-bold mb-4">Wildfire Analysis Result</h2>

            <p className="text-lg mb-2">
              <strong>Image Used:</strong> {result.image_used}
            </p>

            <p className="text-lg mb-2">
              <strong>Probability:</strong> {(result.probability * 100).toFixed(2)}%
            </p>

            <p className="text-lg mb-2">
              <strong>Prediction:</strong> {result.prediction}
            </p>

            <p className="text-xl font-semibold mb-4">
              <strong>Status:</strong>{" "}
              {result.label === "Wildfire" ? (
                <span className="text-red-600">ðŸ”¥ Wildfire Detected</span>
              ) : (
                <span className="text-green-600">âœ” No Wildfire</span>
              )}
            </p>

            <button
              onClick={handleRecalculate}
              className="mt-6 w-full bg-primary text-primary-foreground py-3 rounded-lg hover:bg-primary/90 transition-all"
            >
              Analyze Again
            </button>
          </div>
        </section>
      )}

      {/* Error Section */}
      {result && result.error && (
        <section className="py-12 px-4 md:px-8">
          <div className="max-w-3xl mx-auto bg-red-200 text-red-900 p-6 rounded-lg">
            <h2 className="text-xl font-bold">Error</h2>
            <p>{result.error}</p>
          </div>
        </section>
      )}

      <Footer />
    </div>
  )
}
